* {
    margin: 0;
    font-family: 'Roboto Mono', monospace;
}

html, body {
    width: 100%;
    height: 100%;
}

#wrapper {
    min-height: 100vh;
    overflow: hidden;
    background-color: #e8e8e8;
}

.container {
    position: relative;
    display: flex;
    align-items: center;
    max-width: 1290px;
    height: 100vh;
    margin: 0 auto;
}
